import { Component } from '@angular/core' 

//         Angular2

@Component({
    selector: 'my-app', 

    //  angular2        - (template reference variable) 
    // - -   DOM-     
    // : #var_name,  var_name -  -.        . 
    template: 
    `<div class="panel well">
     <input class="input-lg" #text value="text" /> 
     <h2> {{text.value}}</h2>
     </div>`
	
}) 

export class AppComponent { 

}


